import '../styles/index.scss';
import '../lib/ch32.snackbar.css';
import jQuery from 'jquery';

import '../lib/ch32.snackbar.js';

import Alpine from 'alpinejs';

window.Alpine = Alpine;
window.Alpine.start();

jQuery(document).ready(function () {

    jQuery.ajax({
        url: 'https://api2.binance.com/api/v3/ticker/24hr',
        method: 'GET',
        dataType: 'json',
        success: function (data) {
            console.log(data);
            // loop through the data and create a new div for each product
            for (let i = 0; i < data.length; i++) {
                let hours24 = data[i];
                let hours24Div = document.createElement('tr');
                hours24Div.classList.add('product');
                hours24Div.setAttribute('data-id', hours24.id);
                hours24Div.innerHTML = `
                    <tr class="product-info">
                        <td class="px-6 py-4 whitespace-nowrap">
                        <div class="text-sm font-medium text-gray-900">${hours24.symbol}</div>
                        </td>
                        <td class="px-6 py-4 whitespace-nowrap">${hours24.priceChange}</td>
                        <td class="px-6 py-4 whitespace-nowrap">${hours24.priceChangePercent}</td>
                        <td class="px-6 py-4 whitespace-nowrap">${hours24.weightedAvgPrice}</td>
                        <td class="px-6 py-4 whitespace-nowrap">${hours24.prevClosePrice}</td>
                        <td class="px-6 py-4 whitespace-nowrap">${hours24.count}</td>      
                    </tr>
                `;
                document.querySelector('#products').appendChild(hours24Div);
            }
        },
        error: function (err) {
            console.log(err);
        },
    });

    jQuery(document).ajaxStart(function () {
        jQuery('#loading').show();
    });

    jQuery(document).ajaxComplete(function () {
        jQuery.snackbar({
            text: 'Data Loaded',
            position: 'top-right',
            loader: false,
            stack: true,
            allowSnackbarClose: false,
            icon: 'success',
            hideAfter: 5000,
        });
        jQuery('#loading').hide();
    });

    jQuery(document).on('click', '.copy-toast', function (e) {
        e.preventDefault();

        let time = 3000;

        // Create Settings for Copy Snackbar
        jQuery.snackbar({
            text: 'Unique IBAN copied to clipboard',
            position: 'bottom-center',
            loader: false,
            stack: true,
            allowSnackbarClose: false,
            icon: 'success',
            hideAfter: time,
        });

        // Custom Configurations
        jQuery('.ch32-snackbar-wrap').focus().removeAttr('tabindex').removeAttr('aria-hidden');
        jQuery('.active').removeClass('active');
        jQuery(this).addClass('active');
        jQuery('body').addClass('ch32-snackbar-active');

        setTimeout(function () {
            jQuery('.ch32-snackbar-wrap').attr('tabindex', '-1').attr('aria-hidden', true);
            jQuery('body').removeClass('ch32-snackbar-active');
            jQuery('.active').focus().removeClass('active');
        }, time);
    });

});



